import oakHeader from "../components/header/header";
import oakFooter from "../components/footer/footer";
import oakForm from "../components/form/form";
import { register, officialWebsite } from "../utils/api";
import { Toast } from "vant";
import ans from "@/utils/ans";
import { url } from '../utils/config'
export default {
  name: "IndexPage",
  components: {
    oakHeader,
    oakFooter,
    oakForm,
  },
  data() {
    return {
      url,
      pageShow: false,
      codeText: "",
      num: 60,
      name: "",
      name_error: "",
      company_name: "",
      company_name_error: "",
      phone: "",
      phone_error: "",
      code: "",
      code_error: "",
      list: [
        "<span>一站式权益</span> 低成本 高价值",
        "<span>独家稀缺权益</span> 提升产品竞争力",
        "<span>专业服务</span>   灵活定制",
      ],
      list2: [
        "<span style='display:block'>一站式权益</span> 低成本 高价值",
        "<span style='display:block'>独家稀缺权益</span> 提升产品竞争力",
        "<span style='display:block'>专业服务</span>   灵活定制",
      ],
      current: 0,
      service_value: [
        {
          icon: require("@/static/sv_icon0.png"),
          icon_selected: require("@/static/sv_icon0_selected.png"),
          title: "活跃",
          list: [
            "• 提升用户行为活跃",
            "• 提升用户交易活跃",
            "• 延长用户生命周期",
          ],
        },
        {
          icon: require("@/static/sv_icon1.png"),
          icon_selected: require("@/static/sv_icon1_selected.png"),
          title: "创收",
          list: ["• 获得会员费营收", "• 带动主营业务增收"],
        },
        {
          icon: require("@/static/sv_icon2.png"),
          icon_selected: require("@/static/sv_icon2_selected.png"),
          title: "客户关怀",
          list: [
            "• 通过会员礼，提升品牌价值",
            "• 提升用户尊贵感",
            "• 提升用户归属感",
          ],
        },
        {
          icon: require("@/static/sv_icon3.png"),
          icon_selected: require("@/static/sv_icon3_selected.png"),
          title: "用户画像",
          list: [
            "• 整合多平台消费者数据",
            "• 构建画像与标签体系",
            "• 数据驱动精细化会员运营",
          ],
        },
      ],
      service_value_current: -1,
      showDialog: false,
      project: null,
    };
  },
  head() {
    const local = window.local;
    let title = '橡树黑卡--值得信赖的会员权益平台';
    let description_content = "橡树黑卡为金融、互联网、电商、零售等行业提供强大的用户运营全周期服务，提供积分体系运营、会员营销运营、游戏化运营等方案，400-150-9669。";
    let keywords_content = "橡树黑卡，会员权益，会员积分，视频会员，网站会员，app会员，用户运营，SaaS平台";
    if (local) {
      title = `${local.name}--值得信赖的会员权益平台`;
      description_content = `${local.name}为金融、互联网、电商、零售等行业提供强大的用户运营全周期服务，提供积分体系运营、会员营销运营、游戏化运营等方案，400-150-9669。`;
      keywords_content = `${local.name}，会员权益，会员积分，视频会员，网站会员，app会员，用户运营，SaaS平台`
    }
    return {
      title,
      meta: [
        {
          hid: "index",
          name: "description",
          content: description_content,
        },
        {
          name: "keywords",
          content: keywords_content,
        },
      ],
    };
  },
  watch: {
    name: {
      handler: function () {
        if (this.name) {
          this.name_error = "";
        }
      },
      immediate: true,
    },
    phone: {
      handler: function () {
        if (this.phone) {
          this.phone_error = "";
        }
      },
      immediate: true,
    },
    code: {
      handler: function () {
        if (this.code) {
          this.code_error = "";
        }
      },
      immediate: true,
    },
  },
  mounted() {
    this.pageShow = true;
    this.project = window.local;
    ans.pageView("oak_page");
  },
  methods: {
    getCode() {
      //获取验证码
      if (!this.phone) {
        this.phone_error = "请填写您的联系电话";
        return;
      }
      if (!/^1([3-9])\d{9}$/.test(this.phone)) {
        this.phone_error = "请填写正确的联系电话";
        return;
      }
      Toast.loading({
        message: "加载中...",
        forbidClick: true,
      });
      register(this.$axios, {
        phone_number: this.phone,
        app_name: "橡树黑卡",
      }).then((res) => {
        Toast.clear();
        this.session_code = res.session_code;
        this.timer();
      });
    },
    getPlan() {
      if (!this.name) {
        this.name_error = "请填写您的姓名";
        return;
      } else {
        this.name_error = "";
      }
      if (!this.company_name) {
        this.company_name_error = "请填写公司名称";
        return;
      } else {
        this.company_name_error = "";
      }
      if (!this.phone) {
        this.phone_error = "请填写您的联系电话";
        return;
      } else {
        if (!/^1([3-9])\d{9}$/.test(this.phone)) {
          this.phone_error = "请填写正确的联系电话";
          return;
        }
        this.phone_error = "";
      }
      if (!this.code) {
        this.code_error = "请填写验证码";
        return;
      } else {
        this.code_error = "";
      }
      if (!this.session_code) {
        Toast("请先获取验证码");
        return;
      }
      Toast.loading({
        message: "加载中...",
        forbidClick: true,
      });
      officialWebsite(this.$axios, {
        name: this.name,
        phone_number: this.phone,
        company: this.company_name,
        otp: this.code,
        session_code: this.session_code,
      }).then(() => {
        Toast.clear();
        Toast("提交成功，稍后会有专门商务联络，谢谢！");
        this.phone = "";
        this.company_name = "";
        this.code = "";
        this.name = "";
        ans.track("business_click");
      });
    },
    timer() {
      let time = setInterval(() => {
        this.num--;
        this.codeText = `${this.num}s后重新获取`;
        if (this.num <= 0) {
          clearInterval(time);
          this.codeText = "";
          this.num = 60;
        }
      }, 1000);
    },
  },
};
